<script>
import Header from "../../components/global/header/HeaderArea.vue"
import CitySelecter from "./components/CitySelecter.vue"
import { Toast } from '../../components/global/swal.js'
import { ref, onMounted, watch } from 'vue'
import { apiCaringOrg, apiCaringOrgAllCity } from "@/api/api"

export default {
  setup() {
    let sanatoriumData = ref([])
    let cityAreaData = ref([])
    let city = ref("請選擇縣市")
    let area = ref("請選擇鄉鎮市區")
    let cityIdx = ref(0)
    let areaIdx = ref(0)

    const updateCityIdx = (val) => {
      cityIdx.value = val.value
    }
    const updateAreaIdx = (val) => {
      areaIdx.value = val.value
    }

    // onMounted(async () => {
    //   // call api
    //   sanatoriumData.value = await apiCaringOrgAllCity({ cityId: 8 })
    //   sanatoriumData.value = sanatoriumData.value.data
    //   // console.log(sanatoriumData.value)
    // })

    watch(() => cityIdx.value, async () => {
      sanatoriumData.value = await apiCaringOrgAllCity({ cityId: cityIdx.value })
      sanatoriumData.value = sanatoriumData.value.data
    })
    watch(() => areaIdx.value, async () => {
      sanatoriumData.value = await apiCaringOrg({ cityId: cityIdx.value, areaId: areaIdx.value })
      if (sanatoriumData.value.data.length == 0) {
        Toast("該區域無資料")
        sanatoriumData.value = []
      } else {
        sanatoriumData.value = sanatoriumData.value.data
      }
    })

    return { sanatoriumData, updateCityIdx, updateAreaIdx }
  },
  components: {
    Header, CitySelecter
  },
};
</script>

<template>
  <div class="sanatorium">
    <Header title="安養中心" right="map" path="/map/sanatoriumMap" />
    <div class="hospital">
      <section class="c-main">
        <CitySelecter @city-idx="updateCityIdx" @area-idx="updateAreaIdx" />
        <!-- <div class="ui-tab">
          <div class="row">
            <div class="col">
              <select class="form-control form-select selectpicker">
                <option selected>請選擇縣市</option>
