                <option>桃園市</option>
                <option>臺中市</option>
                <option>臺南市</option>
                <option>高雄市</option>
              </select>
            </div>
            <div class="col">
              <select class="form-control form-select selectpicker">
                <option selected>請選擇鄉鎮市區</option>
                <option>中區</option>
                <option>東區</option>
                <option>西區</option>
                <option>北區</option>
                <option>南區</option>
                <option>西屯區</option>
                <option>北屯區</option>
                <option>南屯區</option>
              </select>
            </div>
          </div>
        </div> -->
        <div class="resource-container">
          <div class="card" v-for="item in sanatoriumData" :key="item.name">
            <div class="card-header">
              <a :href=item.properties.website class="card-link">
                <i class="far fa-link"></i>
              </a>
            </div>
            <div class="card-body">
              <a :href=item.properties.website class="card-link">
                <div class="card-title">{{ item.properties.name }}</div>
                <div class="card-text">
                  {{ item.properties.address }}<br>
                  {{ item.properties.telephone }}
                </div>
              </a>
            </div>
          </div>
        </div>
      </section>
    </div>
  </div>
